import service from '@/api/request';
import { AxiosPromise } from 'axios';
import { NftMetaData, QueryData } from '@/interface/base';
import axios from 'axios'; // 引入axios
import { BlockData } from '@/interface/base';

export interface nftResult {
  status: '1';
  message: 'OK';
  result: Array<BlockData>;
}

export const getNfts = (data: QueryData): AxiosPromise<nftResult> => {
  console.log(data);
  return service({
    url: '/api',
    method: 'get',
    params: data,
  });
};
export const getTokenURI = (uri: string): AxiosPromise<NftMetaData> => {
  const service = axios.create({
    baseURL: uri,
    timeout: 99999,
  });
  return service({
    method: 'get',
  });
};
